<template>
	<view class="talk-comment">
		<view @click="gotoChatDetail(item.talkId)" class="item" v-for="(item,index) in talkCommentList">
			<my-chat-comment @removeTalkComment="removeTalkComment"  :c_item="item"></my-chat-comment>
		</view>
		<myLoad :status="status"></myLoad>
	</view>
</template>

<script>
	import {getTalkCommentPage} from "@/api/talkComment.js"
	import {myChatComment} from "@/components/myChatComment/myChatComment.vue"
	export default {
		data() {
			return {
				status:"more",
				talkCommentList:[],
				// 查询参数
				queryParams: {
					orderType: 2,
					pageNum: 1,
					pageSize: 5,
					talkId:-1
				},
				//总条数
				total:0		
			}
		},
		components:{
			myChatComment
		},
		onLoad(op) {
			this.getTalkCommentList();
		},
		//下拉刷新
		onPullDownRefresh() {
			
		
			this.queryParams.pageNum = 1;
			this.talkCommentList = [];
			this.getTalkCommentList().then(()=>{uni.stopPullDownRefresh()})
		},
		//触底
		onReachBottom(){
			// console.log("触底了")
			this.handleCurrentChange();
		},
		methods: {
			async getTalkCommentList(){
				
				this.status = "loading"
				//拿评论
				const res = await getTalkCommentPage(this.queryParams);
				
				this.talkCommentList = [...this.talkCommentList,...res.data.rows];
				
				this.total =  res.data.total
				
				if(!this.isMore())
					this.status = "nomore"
				else
					this.status = "more"
			},	//分页
			handleCurrentChange(){
				if(!this.isMore()){
					this.status = "nomore"
					return ;
				}
				this.queryParams.pageNum++;
				this.queryParams.pageSize;
				this.getTalkCommentList();
				
			},
			removeTalkComment(id){
				 this.talkCommentList = this.talkCommentList.filter(item=>{return item.id != id})
				 --this.total;
			},
			isMore(){
				if(this.queryParams.pageNum*this.queryParams.pageSize >= this.total){
					
					return false;
				}
				return true;
			},
			gotoChatDetail(id){
				uni.navigateTo({
					url:`/pages/chatDetail/chatDetail?id=${id}`
				})
			},
		}
	}
</script>

<style>
.talk-comment{
	height: 101vh;
	background: ;
}
</style>
